import React, { Component } from 'react';
import ViewObject from "../../util/ViewObject";
import TableCell from "@mui/material/TableCell";
import TableRow from "@mui/material/TableRow";
import {JSONtoStringifyYAML} from "../../util/util";


class AlluxioCluster extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: this.props.name,

            specJSON: this.props.originalJSON['alluxio-cluster-config']['spec'],
            statusJSON: this.props.originalJSON['status'],
        };
    }

    render() {
        const {name, statusJSON, specJSON} = this.state
        const {handleSendRequest} = this.props

        return (
            <TableRow
                key={name}
                sx={{'&:last-child td, &:last-child th': {border: 0}}}
            >
                <TableCell>{name}</TableCell>
                <TableCell component="th" scope="row">{statusJSON.phase}</TableCell>
                <TableCell align="right">
                    <ViewObject
                        objectKind='AlluxioCluster'
                        objectName={name}
                        objectConfigString={JSONtoStringifyYAML(specJSON)}
                        objectStatusString={JSONtoStringifyYAML(statusJSON)}
                        handleSendRequest={handleSendRequest}
                    />
                </TableCell>
            </TableRow>
        );
    }
}

export default AlluxioCluster
